<template>
  <section id="available">
    
    <div class="title_top">
      <div class="top_op_2"></div>
      <div class="top_op">
        <div class="op_left" @click="toIndex('index')">
          <img :src="cdnPath+'available_index.png'" class="available_index" >

        </div>
        <span class="top_op_line"></span>
        <div class="op_right" @click="openShare">
          <img :src="cdnPath+'available_share.png?v=1'" class="available_share" >

        </div>
      </div>
      <img :src="cdnPath+'available_top.png?v=1'" alt="" class="available_top">
      <img :src="cdnPath+'logo@3x.png'" class="available_logo" @click="toIndex('index')">
      <!-- <img :src="cdnPath+'available_share.png'" class="available_share" @click="openShare"> -->
      <img :src="cdnPath+'cloud.png'" class="available_cloud"> 
    </div>

    <div class="title_join_num">
        <div class="join_num">
          <span class="num_span">{{nowNum[0]}}</span>
          <span class="num_span">{{nowNum[1]}}</span>
          <span class="num_span">{{nowNum[2]}}</span>
          <!-- <span class="num_symbol">,</span> -->
          <span class="num_span">{{nowNum[3]}}</span>
          <span class="num_span">{{nowNum[4]}}</span>
          <span class="num_span">{{nowNum[5]}}</span>
          <span class="num_span">{{nowNum[6]}}</span>
        </div>
        <img :src="cdnPath+'join_num_bg.png'" alt="" class="join_num_bg">
    </div>
    
    <div class="side_nav_list">
      <div class="nav_open" v-if="navSideStyle==='close'" >
        <img :src="cdnPath+'open_cont.png'" alt="" class="open_cont_bg" @click="clickSideNav('open')">
      </div>
      <div class="nav_close" v-else>
        <img :src="cdnPath+'to_top.png'" alt="" class="to_top" @click="toAdress">

        <div class="close_cont">
          <div class="nav_list">
            <div class="nav_blo"  @click="sideNavTo('1')">一线大厂</div>
            <div class="nav_blo"  @click="sideNavTo('2')">城市专场</div>
            <div class="nav_blo"  @click="sideNavTo('3')">风口机遇</div>
            <div class="nav_blo"  @click="sideNavTo('4')">热门职位</div>
          </div>
          
          <div class="clo_nav_warp" @click="clickSideNav('close')">
            <img :src="cdnPath+'open_cont2.png'" alt="" class="clo_nav">
          </div>
        </div>
      </div>
    </div>
    <!-- 一线大厂 -->
    <div class="cont_blo topCompanyCont" id="block1">
      <img :src="cdnPath+'blo_tit_one.png'" alt="" class="blo_tit one">
      <img :src="cdnPath+'cloud.png'" alt="" class="cloud_1"> 

      <div class="topCompanyList">
        <div class="top_company_blo" v-for="item, index in batCompanyInfo" @click="clickHotCompany(item.companyId, index)">
          <div class="blo_logo_wrap">
            <img :src="item.companyLogo.middleUrl" class="top_company_icon" v-if="item.companyLogo">
            <!-- item.isHotFavored -->
            <div class="click_num" v-if="item.isHotFavored !== 1">
              <div class="clicked_num">
                <img :src="cdnPath+'hot.png'" alt="" class="hot_icon">
              </div>
              {{item.hotFavorCount}}
            </div>
            <div class="click_num clicked" v-else>
              <img :src="cdnPath+'hot.png'" alt="" class="hot_icon">
              {{item.hotFavorCount}}
            </div>
          </div>
          <p class="top_company_name">{{item.companyShortname?item.companyShortname:item.companyName}}</p>
        </div>
      </div>
    </div>

    <!-- 热门城市 -->
    <div class="cont_blo hotCitiesCont" id="block2">
      <img :src="cdnPath+'blo_tit_two.png'" alt="" class="blo_tit two">
      <img :src="cdnPath+'cloud.png'" alt="" class="cloud_1"> 

      <div class="hotCities">
        <div class="hot_city_nav_wrap ">
          <img :src="cdnPath+'cloud.png'" alt="" class="cloud_2"> 
          <img :src="cdnPath+'clock.png'" alt="" class="clock"> 
          <img :src="cdnPath+'icon_1.png'" alt="" class="icon_1">

          <div class="hot_city_nav" :class="{fixed: showHotCityTop}">
            <div class="city_nav_blo" >
              <div class="nav_blo_left">
                <img :src="cdnPath+'hot_nav_left.png'" alt="" class="nav_blo_left_icon">
                <p class="nav_blo_left_text">城市专场</p>
                
              </div>
              <ul class="city_nav_list">
                <li :class="{'cur':cityNav ==='beijing'}"  @click="clickCityNav('beijing')"><span class="nav_list_text">北京</span></li>
                <li :class="{'cur':cityNav ==='guangzhou'}" @click="clickCityNav('guangzhou')"><span class="nav_list_text">广州</span></li>
                <li :class="{'cur':cityNav ==='shenzhen'}" @click="clickCityNav('shenzhen')"><span class="nav_list_text">深圳</span></li>
                <li :class="{'cur':cityNav ==='shanghai'}" @click="clickCityNav('shanghai')"><span class="nav_list_text">上海</span></li>
              </ul>
            </div>
            
            <div class="city_nav_boder"></div>
          </div>
        </div>
        
        <div class="hot_city " id="beijing">
          <img :src="cdnPath+'beijing.png'" class="city_name " >
          <div class="city_swipe_cont"> 
            <mt-swipe :auto="3000" @change="changeSwipe">
              <mt-swipe-item class="position_list" v-for="list, idx in recommendCompanyList.beijing">
                <div class="position_blo" v-for="item, index in list" @click="toCompanyXcx(item.companyId,item.vkey)">
                  <img :src="item.logoInfo.smallUrl" class="blo_left" v-if="item.logoInfo" >
                  <div class="blo_right">
                    <p class="hot_company_name">{{item.companyShortname}}</p>
                    <div class="hot_company_msg">
                      <div class="textEllipsis">{{item.industry}} · {{item.financingInfo}} </div> 
                      <div class="textEllipsis">  · {{item.employeesInfo}}</div>
                    </div>

                    <img :src="cdnPath+'toRight.png'" class="toRight"> 
                  </div>
                </div>
              </mt-swipe-item>
            </mt-swipe>
          </div>
        </div>

        <div class="hot_city " id="guangzhou">
          <img :src="cdnPath+'guangzhou.png'" class="city_name">
          <div class="city_swipe_cont"> 
            <mt-swipe :auto="3000" @change="changeSwipe2">
              <mt-swipe-item class="position_list" v-for="list, idx in recommendCompanyList.guangzhou" >
                <div class="position_blo" v-for="item, index in list" @click="toCompanyXcx(item.companyId,item.vkey)">
                  <img :src="item.logoInfo.smallUrl" class="blo_left" v-if="item.logoInfo">
                  <div class="blo_right">
                    <p class="hot_company_name">{{item.companyShortname}}</p>
                    <div class="hot_company_msg">
                      <div class="textEllipsis">{{item.industry}} · {{item.financingInfo}} </div> 
                      <div class="textEllipsis">  · {{item.employeesInfo}}</div>
                    </div>

                    <img :src="cdnPath+'toRight.png'" class="toRight"> 
                  </div>
                </div>
              </mt-swipe-item>
            </mt-swipe>
          </div>
        </div>

        <div class="hot_city " id="shenzhen">
          <img :src="cdnPath+'shenzhen.png'" class="city_name ">
          <div class="city_swipe_cont"> 
            <mt-swipe :auto="3000" @change="changeSwipe3">
              <mt-swipe-item class="position_list" v-for="list, idx in recommendCompanyList.shenzhen" >
                <div class="position_blo" v-for="item, index in list"  @click="toCompanyXcx(item.companyId,item.vkey)">
                  <img :src="item.logoInfo.smallUrl" class="blo_left" v-if="item.logoInfo">
                  <div class="blo_right">
                    <p class="hot_company_name">{{item.companyShortname}}</p>
                    <div class="hot_company_msg">
                      <div class="textEllipsis">{{item.industry}} · {{item.financingInfo}} </div> 
                      <div class="textEllipsis">  · {{item.employeesInfo}}</div>
                    </div>

                    <img :src="cdnPath+'toRight.png'" class="toRight"> 
                  </div>
                </div>
              </mt-swipe-item>
            </mt-swipe>
          </div>
        </div>

        <div class="hot_city " id="shanghai">
          <img :src="cdnPath+'shanghai.png'" class="city_name">
          <div class="city_swipe_cont"> 
            <mt-swipe :auto="3000" @change="changeSwipe4">
              <mt-swipe-item class="position_list" v-for="list, idx in recommendCompanyList.shanghai">
                <div class="position_blo" v-for="item, index in list"  @click="toCompanyXcx(item.companyId,item.vkey)">
                  <img :src="item.logoInfo.smallUrl" class="blo_left" v-if="item.logoInfo">
                  <div class="blo_right">
                    <p class="hot_company_name">{{item.companyShortname}}</p>
                    <div class="hot_company_msg">
                      <div class="textEllipsis">{{item.industry}} · {{item.financingInfo}} </div> 
                      <div class="textEllipsis">  · {{item.employeesInfo}}</div>
                    </div>
                    <img :src="cdnPath+'toRight.png'" class="toRight"> 
                  </div>
                </div>
              </mt-swipe-item>
            </mt-swipe>
          </div>
        </div>
      </div>
    </div>

    <!-- 风口上的机遇 -->
    <div class="cont_blo hotCont" id="block3">
      <img :src="cdnPath+'blo_tit_three.png'" class="blo_tit three">
      <img :src="cdnPath+'cloud.png'" class="cloud_1"> 
      <div class="companyList2">
        <img :src="item.logoInfo.smallUrl" class="company_blo"  v-for="item, index in cityHotCompanyList" @click="toCompanyXcx(item.companyId,item.vkey)">
      </div>
    </div>

    <!-- 热门职位 -->
    <div class="cont_blo positionCont" id="block4">
      <img :src="cdnPath+'blo_tit_four.png'" class="blo_tit four">
      <img :src="cdnPath+'icon_2.png'" class="icon_1"> 
      <img :src="cdnPath+'cloud.png'" class="cloud_1"> 
      <div class="positionList">
        <div class="positionBlo" v-for="item, index in recommendPositionList" @click="toPositionXcx(item.positionId,item.vkey)">
          <div class="post_blo_tit">
            <div class="blo_job_name textEllipsis">{{item.positionName}}</div>
            <div class="blo_money">{{item.emolumentMin+'-'+item.emolumentMax+'k'}}</div>
          </div>
          <div class="blo_comp_msg">
            <span v-if="item.city">{{item.city}}</span>
            <span v-if="item.workExperienceName">{{item.workExperienceName}}</span>
          </div>
          <div class="post_blo_tit2">
            <img :src="item.logoInfo.smallUrl" class="blo_tit2_icon">
            {{item.companyShortname}}
          </div>
        </div>
      </div>

      <img :src="cdnPath+'toMorePosition.png'" class="toMorePosition"  @click="toIndex('toMorePosition')">
    </div>

    <!-- 合作伙伴 -->
    <div class="cont_blo cooperation" >
      <img :src="cdnPath+'blo_tit_five.png'" class="blo_tit five">
      <img :src="cdnPath+'icon_1.png'" class="icon_1"> 
      <img :src="cdnPath+'cloud.png'" class="cloud_1"> 
      <img :src="cdnPath+'banner.png?v=1'" class="cooperation_icon"> 
    </div>

    <footer>
      <p class="btn_hint">*点击下方按钮，马上进驻本期招聘节</p>
      <img :src="cdnPath+'company_join.png'" alt="" class="company_join_btn" @click="toTable">
      <span class="btn_hint2">咨询请致电：<a href="tel:4000655788">400-065-5788</a></span>

    </footer>


    <div class="pop_share" v-if="pop.isShow" @click="pop.isShow = false">
      <div class="share_cont" v-if="pop.showType ==='two'">
        <img :src="cdnPath+'toShare.png'" class="toShare">
        <div class="cont_text">
          <p class="">1、点击 ··· </p>
          <p class="">2、点击转发按钮，即可分享</p>
        </div>
      </div>

      <div class="choseBox"   v-if="pop.showType==='one'">
        <div class="btn_warp" @click.stop="pop.showType='two'">
          <div class="btnCon btn1">
            <div class="title">分享给好友或群聊<i class="icon">一键访问</i></div>
          </div>
        </div>
        <div class="btn_warp" @click="toSharePic">
          <div class="btnCon btn2">
            <div class="title">生成朋友圈分享图<i class="icon">发圈必备</i></div>
          </div>
        </div>
        <img :src="cdnPath+'clo.png'" class="clo">
      </div>
    </div>
  </section>
</template>
<style lang="less">
  @import "./available.less";
  .choseBox {
    width:345px;
    background:rgba(255,255,255,1);
    border-radius:12px 0px 12px 12px;
    border:1px solid rgba(0,0,0,1);
    box-sizing: border-box;
    padding: 6px 30px;
    position: absolute;
    bottom: 25px;
    left: 50%;
    margin-left: -172.5px;
    .btn_warp {
      .close {
        width:22px;
        height:17px;
        background:rgba(255,220,41,1);
        border-radius:50px 50px 0px 0px;
        border:1px solid rgba(0,0,0,1);
        position: absolute;
        right: -1px;
        top: -17px;
        background: #FFDC29;
        overflow: hidden;
        text-align: center;
        z-index: 11;
        box-sizing: border-box;
        padding: 0;
        .icon {
          font-size: 12px;
          line-height: 12px;
          color: #282828;
          display: block;
          margin: 6px 0;
        }
      }
      .btnCon {
        padding: 26px 0;
        text-align: left;
        border-bottom:1px solid rgba(232,233,235,1);
        background: #fff url('https://lieduoduo-uploads-test.oss-cn-shenzhen.aliyuncs.com/front-assets/images/btn_share_wechat.png') no-repeat right center;
        background-size: 38px 38px;
        .title {
          line-height: 20px;
          font-size: 16px;
          font-weight:500;
          color:rgba(40,40,40,1);
          .icon {
            padding: 2.5px 7.5px;
            font-size: 13px;
            font-weight:500;
            color:#572C84;
            line-height:15px;
            border-radius:10px;
            border:1px solid #572C84;
            vertical-align:2px;
            margin-left: 10px;
            box-sizing: border-box;
            display: inline-block;
          }
        }
        &.btn2 {
          background-image: url('https://attach.lieduoduo.ziwork.com/front-assets/available/friend.png');
          border-bottom:none;
        }
      }
    }
    .clo {
      width:44px;
      height:33px;
      position: absolute;
      right: 0;
      top: -33px;
    }
  }
  .pop_share {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1000;
    background: rgba(0,0,0,0.6) ;
    .share_cont {
      position: absolute;
      top: 25px;
      right: 20px;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      .toShare {
        width:45px;
        height:45px;
        display: block;
        position: relative;
        right: 50px;
        margin-bottom: 10px;
      }
      .cont_text {
        width: 267px;
        height: 88px;
        background:rgba(255,255,255,1);
        border:2px solid rgba(101,39,145,1);
        border-radius:5px;
        padding: 10px  0px 0px 15px;
        box-sizing: border-box;
        p {
          font-size:18px;
          font-family:SourceHanSansCN-Bold;
          font-weight:bold;
          color:rgba(35,18,53,1);
          &:last-child {
            margin-top: 10px;
          }
        }
      }
    }

  }
</style>
<script>
import Vue from 'vue'
import Component from 'vue-class-component'
import axios from 'axios'
import wechatMixin from '@/mixins/wechat'
import { Swipe, SwipeItem } from 'mint-ui';
import {getIndexMsgApi, getHotCompanyListApi, favorApi, addApplyApi, touchApi} from '@/store/api/available'
import localstorage from '@/utils/localstorage'

Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);

const wx = Vue.wx
@Component({
  components: {
  },
  mixins: [wechatMixin]
})
export default class PageIndex extends Vue {
  //侧边栏展示
  navSideStyle = 'close'
  // 城市nav
  cityNav = 'beijing'
  // 是否显示返回顶部按钮
  showBackTop = false
  // 是否像是悬浮框
  showBar = true
  // 是否首次到达动画框位置
  isFirstReach = false
  // cdn 图片路径
  cdnPath = `${process.env.VUE_APP_CDNPATH}/available/`

  // 分享的数据
  infos = {}
  recommendPositionList = []  // 推荐职位
  partnerCompanyList = []   // 合作伙伴
  cityHotCompanyList = []   // 热门公司
  batCompanyInfo = []   // 一线大厂
  vkeyArr = [] // vkey 按钮
  joinTotal = 0
  nowNum = [0,0,0,0,0,0,0]  // 参与人数
  hotCityData = {
    beijing: {
      page:1,
      total:0
    },
    shanghai: {
      page:1,
      total:0
    },
    guangzhou: {
      page:1,
      total:0
    },
    shenzhen: {
      page:1,
      total:0
    },
    count: 5,
  }
  showHotCityTop = false  //城市专场是否置顶
  pop = {
    isShow: false,
    showType: 'one'
  }
  isShowShare = false

  recommendCompanyList = {
    beijing: [],
    shanghai: [],
    guangzhou: [],
    shenzhen: []
  }
  mounted () {
    wx.miniProgram.getEnv(function (res) {
      // console.log(res.miniprogram) // true
      wx.miniProgram.postMessage({data: 2222})
    })
    localstorage.set('sessionToken', this.$route.query.sessionToken)
    localstorage.set('token', this.$route.query.token)
    this.appendHead()
    setTimeout(() => {
      this.getList()
      this.firstGetCityCompany()
    }, 500)
    this.wechatShare({
      title: '猎多多  社交求职招聘神器',
      desc: '招聘营销变得必不可少，一起开启你的社交招聘',
      link: location.href.split('#')[0],
      imgUrl: `${this.cdnPath}share_img.png`
    })
  }
  clickCityNav(style) {
    let num = this.getElementTop(document.querySelector('#'+style))
    if(this.cityNav === style){
      return
    }
    this.toAdress(num-150)
    this.cityNav = style
  }

  // 统计
  touchStatistics(vkey){
    let data = {
      vkey : vkey,
      from : 1,
    }
    touchApi(data).then(res => {
      console.log(res)
    })
  }

  setScroll() {
    let hot_city_nav = document.querySelector('.hotCitiesCont').offsetTop
    let hotCont = document.querySelector('.hotCont').offsetTop
    let clientHeight = this.getClientHeight()
    let beijing = this.getElementTop(document.querySelector('#beijing'))-200
    let shanghai = this.getElementTop(document.querySelector('#shanghai'))-200
    let guangzhou = this.getElementTop(document.querySelector('#guangzhou'))-200
    let shenzhen = this.getElementTop(document.querySelector('#shenzhen'))-200

    window.onscroll = (() => {
      let scrollTop = this.getNowScrollTop()
      if (scrollTop >= hot_city_nav+114 && scrollTop < hotCont) {
        this.showHotCityTop = true
      }else if (scrollTop >= hotCont) {
        this.showHotCityTop = false
      } else {
        this.showHotCityTop = false
      }

      if (scrollTop > beijing && scrollTop < guangzhou) {
        this.cityNav = 'beijing'
      } else if(scrollTop > guangzhou && scrollTop < shenzhen) {
        this.cityNav = 'guangzhou'
      } else if(scrollTop > shenzhen && scrollTop < shanghai) {
        this.cityNav = 'shenzhen'
      } else if(scrollTop > shanghai) {
        this.cityNav = 'shanghai'
      }
    })
  }

  // 第一次进入热门城市
  firstGetCityCompany() {
    this.getHotCompanyList(1,'guangzhou')
    this.getHotCompanyList(1,'shenzhen')
    this.getHotCompanyList(1,'shanghai')
  }

  getList() {
    let data = {
      count: 20,
      page: 1
    }
    getIndexMsgApi().then(res => {
      const {recommendPositionList, recommendCompanyList, partnerCompanyList ,cityHotCompanyList, batCompanyInfo, vkeyArr, joinTotal } = res.data

      this.recommendPositionList = recommendPositionList
      this.partnerCompanyList = partnerCompanyList
      this.cityHotCompanyList = cityHotCompanyList
      this.batCompanyInfo = batCompanyInfo
      this.joinTotal = joinTotal
      this.vkeyArr = vkeyArr
      this.hotCityData['beijing'].total = Math.ceil(recommendCompanyList.meta.total/this.hotCityData.count)
      this.joinNumOp()
      let ary = new Array(this.hotCityData['beijing'].total)
      ary[0] = recommendCompanyList.companies
      this.recommendCompanyList.beijing = ary
      wx.miniProgram.postMessage({data: res.data})
      setTimeout(this.setScroll,1500)
    }).catch(err => {
    })
  }

  getHotCompanyList(page,city) {
    let cityC = ''

    switch(city){
      case 'beijing':
        cityC = '北京'
        break

      case 'guangzhou':
        cityC = '广州'
        break

      case 'shanghai':
        cityC = '上海'
        break

      case 'shenzhen':
        cityC = '深圳'
        break
    }

    let data = {
      city: cityC,
      count: this.hotCityData.count,
      page: page || 1
    }
    
    getHotCompanyListApi(data).then(res => {
      if(data.page !== 1){
        this.recommendCompanyList[city][data.page-1] = res.data.companies
        return
      }
      this.hotCityData[city].total = Math.ceil(res.meta.total/this.hotCityData.count)
      let ary = new Array(this.hotCityData[city].total)
      ary[0] = res.data.companies
      this.recommendCompanyList[city] = ary
    }).catch(err => {
    })
  }

  changeSwipe(index){
    let data = this.recommendCompanyList['beijing'][index]
    if (!data){
      this.getHotCompanyList(index+1,'beijing')
    }
  }

  changeSwipe2(index){
    let data = this.recommendCompanyList['guangzhou'][index]
    if (!data){
      this.getHotCompanyList(index+1,'guangzhou')
    }
  }

  changeSwipe3(index){
    let data = this.recommendCompanyList['shenzhen'][index]
    if (!data){
      this.getHotCompanyList(index+1,'shenzhen')
    }
  }

  changeSwipe4(index){
    let data = this.recommendCompanyList['shanghai'][index]
    if (!data){
      this.getHotCompanyList(index+1,'shanghai')
    }
  }

  toSharePic() {
    let imgUrl = encodeURIComponent('https://attach.lieduoduo.ziwork.com/front-assets/images/zpjShare.jpg')

    wx.miniProgram.navigateTo({
      url: `../poster/saveImg/saveImg?url=${imgUrl}`
    })
  }

  sideNavTo (style) {
    let num = this.getElementTop(document.getElementById('block'+style))
    this.toAdress(num)
  }

  getElementTop(element) {
    var actTop = element.offsetTop;
    var current = element.offsetParent;
    while(current !== null){
        actTop += current.offsetTop;
        current = current.offsetParent;
    }
    return actTop;
  }

  

  //设置人数增长配置
  setNumConfig() {
    let myDate = new Date()
    let hour = myDate.getHours()
    let minutes = myDate.getMinutes()
    let data = {
      type: 1, //1低峰 2中锋 3高峰
      random: [0,1],
      time: 5
    }

    if (hour<8) {
      data.type = 1
    } else if(hour>6 && hour<10) {
      data.type = 3
    } else if(hour>11 && hour<15) {
      data.type = 3
    } else if(hour>19 && hour<23) {
      data.type = 3
    } else if(hour>9 && hour<13) {
      data.type = 2
    } else if(hour>13 && hour<21) {
      data.type = 2
    } else if(hour>19) {
      data.type = 2
    }

    switch(data.type) {
      case 1:
        data.random = [0,2]
        break
      case 2:
        data.random = [4,6]
        break

      case 3:
        data.random = [9,11]
        break
    }
    return data
  }

  // 获取指定范围随机数
  randomNum(minNum,maxNum){ 
    switch(arguments.length){ 
      case 1: 
          return parseInt(Math.random()*minNum+1,10)
      break; 
      case 2: 
          return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10) 
      break; 

      default: 
          return 0; 
      break; 
    } 
  } 

  joinNumOp() {
    this.addNum()
    setInterval(this.addNum,3000)
  }

  addNum() {
    let config = this.setNumConfig() //获取配置
    let randomNum = config.type === 1 ? Math.round(Math.random()) : this.randomNum(config.random[0], config.random[1]) // 随机数
    let num = this.joinTotal + randomNum
    let num2 = this.joinTotal.toString()
    let ary = num2.split('')

    for(var i = ary.length;7>i;i++){
      ary.unshift(0)
    }
    this.nowNum = ary
    this.joinTotal = num
  }

  // 一线大厂点击
  clickHotCompany(id,index) {
    let item = this.batCompanyInfo[index]
    this.touchStatistics(item.vkey)
    if(!item.isHotFavored){
      favorApi({company_id: id}).then(res => {
        item.isHotFavored = 1
      })
      item.hotFavorCount+=1
    }
    this.toCompanyXcx(id)
  }

  // 去小程序职位
  toPositionXcx(id, vkey) {
    if (vkey) this.touchStatistics(vkey)

    wx.miniProgram.navigateTo({
      url: `../positionDetail/positionDetail?positionId=${id}&sourceType=zpj`
    })

  }

  // 去公司职位
  toCompanyXcx(id,vkey) {
    if (vkey) this.touchStatistics(vkey)
    wx.miniProgram.navigateTo({
      url: `../homepage/homepage?companyId=${id}`
    })
  }

  toIndex(style) {

    if (style === 'index')  this.touchStatistics(this.vkeyArr.goBackIndex.vkey)
    else if (style === 'toMorePosition') this.touchStatistics(this.vkeyArr.discoverMorePosition.vkey)

    wx.miniProgram.reLaunch({
      url: `../../../../page/applicant/pages/index/index?sourceType=zpj`
    })
  }

  // 点击侧边栏
  clickSideNav(style) {
    if(this.navSideStyle === style){
      return
    }
    this.navSideStyle = style
  }

  toAdress (num) {
    window.scrollTo({ 
      top: num || 0, 
      behavior: "smooth" 
    });
  }

  openShare () {
    this.pop = {
      isShow: true,
      showType: 'one'
    }
  }

  closeShare () {
    this.isShowShare = false
  }

  toTable() {
    this.touchStatistics(this.vkeyArr.applyCompany.vkey)
    this.$router.push({
      name: 'availableTable'
    })
  }

  addApply(){
    addApplyApi({
      company_name: '',
      name: '',
      position: '',
      mobile: ''
    }).then(res => {

    })
  }

  /**
   * @Author   小书包
   * @DateTime 2018-10-26
   * @detail   获取当前滚动的高度
   * @return   {[type]}   [description]
   */
  getNowScrollTop() {
    let scrollTop = 0
    if(document.documentElement && document.documentElement.scrollTop) {
      scrollTop = document.documentElement.scrollTop
    } else if(document.body) {
      scrollTop = document.body.scrollTop
    }
    return scrollTop
  }
  /**
   * @Author   小书包
   * @DateTime 2018-10-26
   * @detail   获取客户端的视口高度
   * @return   {[type]}   [description]
   */
  getClientHeight() {
    let clientHeight = 0
    if(document.body.clientHeight && document.documentElement.clientHeight) {
      clientHeight = (document.body.clientHeight < document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight
    } else {
      clientHeight = (document.body.clientHeight>document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight
    }
    return clientHeight
  }
  /**
   * @Author   小书包
   * @DateTime 2019-03-04
   * @detail   添加网站的关键词和描述
   * @return   {[type]}   [description]
   */
  appendHead() {
    var head = document.getElementsByTagName('head')[0]
    // 添加关键词
    var meta_keywords = document.createElement('meta')
    meta_keywords.name = 'keywords'
    meta_keywords.content = 'meta_keywords'
    head.appendChild(meta_keywords)

    var meta_description = document.createElement('meta')
    meta_description.name = '猎多多  社交求职招聘神器'
    meta_description.content = '招聘营销变得必不可少，一起开启你的社交招聘'
    head.appendChild(meta_description)
  }

  //判断是否是微信浏览器的函数
  get isWeiXin() {
    var ua = window.navigator.userAgent.toLowerCase();
    return ua.match(/MicroMessenger/i) == 'micromessenger'
  }
}
</script>
